.profile {
  width: 100%;
  max-width: 880px;
  padding: 0;
  margin: 0 auto 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 880px) {
  .profile {
    max-width: 282px;
    margin: 42px auto 36px;
    flex-direction: column;
  }
}
